:root {
  --primaryColor: rgb(228, 105, 23);
  --baseColor: rgb(243, 243, 243);
  --black: rgb(41, 41, 41);
  --formColor: rgb(231, 231, 231);
  /* --formColor:               rgb(248, 232, 201); */
  --buttonColor: #000000;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
}

body {
  width: 100%;
  overflow-x: hidden;
  background-color: var(--baseColor);
}

main {
  min-height: "100%";
  position: "relative";
}

a {
  text-decoration: none;
  color: black;
}
